import React from "react";
import { Switch, Route } from "react-router-dom";

import WeeklyAnimeList from "../components/AnimeList/WeeklyAnimeList/WeeklyAnimeList";
import SeasonAnimeList from "../components/AnimeList/SeasonAnimeList/SeasonAnimeList";
import AnnualAnimeList from "../components/AnimeList/AnnualAnimeList/AnnualAnimeList";
import Home from "../pages/Home/Home";
import FavoritedAnimeList from "../components/AnimeList/FavoritedAnimeList/FavoritedAnimeList";
import ErrorPage from "../pages/Error/Error";
import FullAnimeInfo from "../components/FullAnimeInfo/FullAnimeInfo";

const Router = (props) => {
  return (
    <React.Fragment>
      <Switch>
      <Route path={process.env.PUBLIC_URL +  "/"} exact component={Home} />
        <Route path={process.env.PUBLIC_URL +  "/weekly"} exact component={WeeklyAnimeList} />
        <Route path={process.env.PUBLIC_URL +  "/favorite"} exact component={FavoritedAnimeList} />
        <Route path={process.env.PUBLIC_URL +  "/error"} exact component={ErrorPage} />
        <Route path={process.env.PUBLIC_URL +  "/bangumi/:bgmId"} component={FullAnimeInfo}/>
        <Route path={process.env.PUBLIC_URL +  "/:year/:month"} component={SeasonAnimeList} />
        <Route path={process.env.PUBLIC_URL +  "/:year"} component={AnnualAnimeList} />
        <Route path={process.env.PUBLIC_URL +  "*"} component={ErrorPage} />
      </Switch>
    </React.Fragment>
  );
};

export default Router;
